import NIM from './NIM_SDK.js';
import Vue from 'vue';
const appKey = "aabe2bca19bf374ff36bf25834cb5f95";

export var data = {};
var yx = null;
export function initYunXin() {
	if (uni.getStorageSync('yunXinInfo')) {
		yx = NIM.getInstance({
			debug: false,
			appKey,
			account: uni.getStorageSync('yunXinInfo').accid, //accid
			token: uni.getStorageSync('yunXinInfo').token, //token
			db: false, //falseSDKtrue
			isRoamingable: true,
			isHistoryable: true,
			isSyncable: true,
			onconnect: function() {
				console.log('');
			},
			onwillreconnect: function(obj) {
				//  SDK , , 
				console.log('');
				console.log(obj.retryCount);
				console.log(obj.duration);
			},
			ondisconnect: onDisconnect,
			onerror: function(error) {
				console.log(error);
			},
			// 
			onloginportschange: onLoginPortsChange,
			// 
			onblacklist: onBlacklist,
			onsyncmarkinblacklist: onMarkInBlacklist,
			onmutelist: onMutelist,
			onsyncmarkinmutelist: onMarkInMutelist,
			// 
			onfriends: onFriends,
			onsyncfriendaction: onSyncFriendAction,
			// 
			onmyinfo: onMyInfo,
			onupdatemyinfo: onUpdateMyInfo,
			onusers: onUsers,
			onupdateuser: onUpdateUser,
			// 
			onteams: onTeams,
			onsynccreateteam: onCreateTeam,
			onUpdateTeam: onUpdateTeam,
			onteammembers: onTeamMembers,
			// onsyncteammembersdone: onSyncTeamMembersDone,
			onupdateteammember: onUpdateTeamMember,
			// 
			onTeamMsgReceipt: onTeamMsgReceipt,
			// 
			onsessions: onSessions,
			onupdatesession: onUpdateSession,
			// 
			onroamingmsgs: onRoamingMsgs,
			onofflinemsgs: onOfflineMsgs,
			onmsg: onMsg,
			// 
			onofflinesysmsgs: onOfflineSysMsgs,
			onsysmsg: onSysMsg,
			onupdatesysmsg: onUpdateSysMsg,
			onsysmsgunread: onSysMsgUnread,
			onupdatesysmsgunread: onUpdateSysMsgUnread,
			onofflinecustomsysmsgs: onOfflineCustomSysMsgs,
			oncustomsysmsg: onCustomSysMsg,
			// 
			onbroadcastmsg: onBroadcastMsg,
			onbroadcastmsgs: onBroadcastMsgs,
			// 
			onsyncdone: onSyncDone
		});
		Vue.prototype.yx = yx;
	} else {
		console.log('no yunXin info!!')
	}
}

function onDisconnect(error) {
	//  SDK , , 
	console.log('');
	console.log(error);
	if (error) {
		switch (error.code) {
			// , 
			case 302:
				goLogin('token')
				break;
				// , , 
			case 417:
				goLogin('')
				break;
				// , 
			case 'kicked':
				goLogin('')
				break;
			default:
				break;
		}
	}
}

function goLogin(info) {
	uni.navigateTo({
		url: '/pages/login/login?info=' + info
	})
}

